html[dir="rtl"] body {
  text-align: right; }
  html[dir="rtl"] body * {
    letter-spacing: 0 !important; }

.nav_box.box_center_ {
  right: -6vw;
  left: auto; }
  @media only screen and (max-width: 767px) {
    .nav_box.box_center_ {
      right: 0; } }

.b_item .title_ {
  line-height: 1.3; }

.home-banner .search_box .input_box:after {
  left: -1.25rem;
  right: auto; }

.home-banner .search_box .input_box .value_box:after {
  left: 0;
  right: inherit; }

.news_letter_box .form_box .input_box label.error {
  right: 0;
  left: inherit; }

.title_box .title_small {
  margin-bottom: 0.5rem; }
  .title_box .title_small:after {
    right: 0;
    left: auto; }

.inView .title_box .title_small:after {
  right: calc(100% - 6px); }

.about_wrapper .image_grid_box .main_img_box {
  right: 0;
  left: auto; }

.about_wrapper .image_grid_box .thu_img_box {
  left: 0;
  right: auto; }

.b_social_box {
  right: 1.5rem;
  left: auto; }
  @media only screen and (min-width: 1025px) and (max-width: 1370px) and (min-height: 500px) and (max-height: 670px) {
    .b_social_box {
      right: 1rem; } }
  @media only screen and (max-width: 1024px) {
    .b_social_box {
      right: 1rem; } }

.number_wrap .n_item .title_ {
  line-height: 1.5; }

.projects_wrapper .p_item .inner_ .img_box {
  border-radius: 0 0 0 7rem; }

.projects_wrapper .p_item .inner_:hover .logo_box {
  transform: translateZ(20px) translateX(1rem) translateY(-1rem); }

.communities_image_wrap {
  transform: translateX(-100%); }
  .communities_image_wrap .c_item .img_box {
    border-radius: 0 5rem 0 0; }
    @media only screen and (max-width: 980px) {
      .communities_image_wrap .c_item .img_box {
