    @media only screen and (max-width: 767px) {
      .communities_image_wrap .c_item .img_box {
        border-radius: 2rem 2rem 0 0; } }
  .communities_image_wrap .c_item.swiper-slide-next {
    right: 7rem;
    left: auto; }
    @media only screen and (max-width: 980px) {
      .communities_image_wrap .c_item.swiper-slide-next {
        right: 0; } }
  .communities_image_wrap .c_item.swiper-slide-next + .swiper-slide {
    right: 14rem;
    left: auto; }
    @media only screen and (max-width: 980px) {
      .communities_image_wrap .c_item.swiper-slide-next + .swiper-slide {
        right: 0; } }

/* ------------ */
.home-banner .img-wrapper {
  transform: translateX(-20vw); }

.more-wrap a:after {
  left: auto;
  right: calc(100% + 1em); }

.media-tab .nav-links .button-prev {
  margin-left: 1em;
  margin-right: 0; }

.button-prev,
.button-next {
  transform: scaleX(-1); }

.media-tab .nav-links .more-wrap {
  transform: translateX(100%); }

.more-wrap:hover a:after {
  right: 0;
  left: auto; }

.more-wrap a:after {
  transition: right 0.5s 0.4s, width 0.2s 0s, height 0.2s 0.2s; }

.more-wrap.anm-short a:after {
  transition: right 0.2s 0.4s, width 0.2s 0s, height 0.2s 0.2s; }

.side-button {
  left: 0;
  right: auto;
  transform: rotate(90deg) translateY(-50%);
  transform-origin: left; }

.apply-box .more-wrap {
  margin: auto -4em auto auto; }

@media only screen and (max-width: 700px) {
  .home-banner .img-wrapper {
    transform: none; }
  .home-banner .swiper-slide .banner-text .more-wrap {
    transform: translateX(1em); }
  .about-box .more-wrap {
    transform: translateX(2em); } }

.operate-box .box-1 {
  text-align: right; }

.home-banner .swiper-slide .banner-text {
  text-align: right; }

.more-wrap .more-link:after,
.more-wrap a:after,
.wizard > .actions li .more-link:after,
.wizard > .actions li a:after {
  transition: right 0.5s 0.4s, width 0.2s 0s, height 0.2s 0.2s; }

.about-box .box .text_ .qt {
  right: -2rem;
  left: auto; }

.home-banner .swiper-slide .banner-text h2,
.title-box .section-title,
.init-wrap .box h2 {
  line-height: 1.1; }

.media-tab .top-box h2 {
  line-height: normal; }

@media only screen and (min-width: 720px) {
  .about-box .more-wrap .more-link {
    transform: translateX(1em); } }

.about-box .icon {
  margin-left: 0;
  margin-right: 5em; }

.about-box .icon svg {
  transform: scaleX(-1); }

.about-box .icon {
  margin-left: 0;
  margin-right: 0; }

@media only screen and (max-width: 720px) {
  .about-box .box.box-1 {
    align-items: center; }
  .about-box .box.box-1 .section-title {
    text-align: center !important; }
  .about-box .box.box-1 .title-box {
    align-items: center; }
  .about-box .button-wrap {
    margin: 1em 0; }
  .about-box .box .text_ .qt {
    right: 1rem;
    left: auto; } }

@media only screen and (min-width: 720px) {
  .apply-box .bg-text h3 {
    font-size: 10em; }
  .apply-box .bg-text h2 {
    font-size: 10em;
    margin-bottom: 0.2em; }
  .apply-box .bg-text {
    bottom: 0;
    top: 0;
    margin: auto; } }

.media-tab .r-tabs-nav .r-tabs-tab a:-moz-read-only {
  color: #bcc3cf;
  opacity: 1; }

.media-tab .r-tabs-nav .r-tabs-tab.r-tabs-state-active a:-moz-read-only {
  color: #b68a34; }

.apply-box .bg-text h2:-moz-read-only,
.apply-box .bg-text h3:-moz-read-only {
  color: #f7f7f9;
  opacity: 1; }

@media only screen and (min-width: 720px) {
  .apply-box .bg-text h2,
  .apply-box .bg-text h3 {
    margin-bottom: 0.2em;
    margin-top: 0.2em; }
  .apply-box .bg-text:before,
  .apply-box .bg-text:after {
    top: 0;
    bottom: 0; } }

@media only screen and (max-width: 720px) {
  .apply-box .bg-text {
    top: 0;
    bottom: 0;
    margin: auto; } }

.month-next,
.month-prev {
  transform: scaleX(-1); }

.input_box .input-field > label {
  border-right: none; }
  @media only screen and (min-width: 720px) {
    .input_box .input-field > label {
      border-left: 2px solid #b68a34; } }

.project-tabs .tab-head > li {
  border-right: none; }
  @media only screen and (min-width: 720px) {
    .project-tabs .tab-head > li {
      border-left: 2px solid #b68a34; } }

