  margin-right: 5em; }
  @media only screen and (max-width: 767px) {
    footer .footer-2 .icon-list {
      margin-left: 0;
      margin-right: 0; } }

header .notification ul::after {
  left: -1em;
  right: auto; }

header .menu-row .mbox-1.logo .second-logo {
